@import "@/assets/styles/_variables.scss";

    .char {
        &__block {
            @include content;

            padding-top: 8px;
            padding-bottom: 8px;

            &.fixed {
                position: sticky;
                top: 40px;
                background-color: $white;
                z-index: 100;
                border-bottom: 1px solid $gray;
            }
        }

        &__row {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 8px;

            & > * {
                margin-right: 8px;

                &:last-child {
                    margin-right: 0;
                }
            }

            &.stats {
                text-align: center;
                justify-content: flex-start;
                margin: 8px auto 0;
            }

            &.base {
                justify-content: space-between;

                > * {
                    margin-right: 0;

                    &:nth-child(n + 4) {
                        margin-bottom: 0;
                    }
                }
            }
        }

        &__stats {
            @include content;

            padding-top: 12px;
            padding-bottom: 12px;
        }

        &__pb {
            border: 1px solid $gray;
            padding: 8px 12px;
            border-radius: 4px;
            margin-bottom: 8px;
            position: relative;
            width: calc(100% / 3 - (16px / 3.1));
            min-height: 86px;

            &-name {
                text-align: center;
            }

            &-value {
                text-align: center;
                font-size: 24px;
                font-weight: 600;
                margin-bottom: 8px;
                border-bottom: 1px solid $gray;
                padding-bottom: 8px;
            }
        }

        &__spd, &__init, &__tempHp {
            border: 1px solid $gray;
            padding: 8px 12px;
            border-radius: 4px;
            margin-bottom: 8px;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: calc(100% / 3 - (16px / 3));
            min-height: 86px;

            svg {
                position: absolute;
                left: 8px;
                top: 8px;
            }

            *:not(svg) {
                text-align: center;
                position: relative;
            }

            &-value {
                text-align: center;
                font-size: 32px;
                font-weight: 600;
            }
        }

        &__pasPer, &__ins, &__hitDice {
            border: 1px solid $gray;
            border-radius: 4px;
            flex-wrap: nowrap;
            width: 100%;

            &-name {
                text-align: center;
                border-right: 1px solid $gray;
                padding: 8px 12px;
                flex: 1 1 100%;
                margin: 0;
            }

            &-value {
                text-align: center;
                padding: 8px 12px;
                font-weight: 600;
            }
        }

        &__stat {
            width: calc(100% / 4 - 6px);
            margin-bottom: 8px;
            border: 1px solid $gray;
            padding: 8px 12px;
            border-radius: 4px;

            &:nth-child(4) {
                margin-right: 0;
            }

            &-plus {
                text-align: center;
                font-size: 22px;
                font-weight: 600;
                margin-bottom: 8px;
                border-bottom: 1px solid $gray;
                padding-bottom: 8px;
            }

            &-name {
                font-size: 14px;
                margin-top: 4px;
            }
        }

        &__dead-or-live {
            width: calc((100% / 4 - 6px) * 2 + 8px);
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            margin-bottom: 8px;

            &__row {
                display: flex;
                align-items: center;

                .svg-icon {
                    margin: 0 8px 0 16px;
                    cursor: pointer;
                }

                .checkbox {
                    margin-left: 8px;
                    width: 18px;
                    height: 18px;
                }
            }
        }

        &__hp {
            border: 1px solid $gray;
            padding: 8px 12px;
            border-radius: 4px;
            margin-bottom: 8px;
            position: relative;
            width: calc(100% / 3 - (16px / 3));
            min-height: 86px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            svg {
                position: absolute;
                left: 8px;
                top: 8px;
            }

            *:not(svg) {
                text-align: center;
                position: relative;
                width: 100%;
            }

            &-now {
                text-align: center;
                font-size: 24px;
                font-weight: 600;
                margin-bottom: 8px;
                border-bottom: 1px solid $gray;
                padding-bottom: 8px;
            }

            &-value {
                text-align: center;
                font-size: 32px;
                font-weight: 600;
            }
        }

        &__armor {
            border: 1px solid $gray;
            padding: 8px 12px;
            border-radius: 4px;
            position: relative;
            width: calc(100% / 3 - (16px / 3));
            min-height: 86px;

            svg {
                position: absolute;
                left: 8px;
                top: 8px;
            }

            *:not(svg) {
                text-align: center;
                position: relative;
            }

            &-initial {
                font-size: 24px;
                font-weight: 600;
                margin-bottom: 8px;
                border-bottom: 1px solid $gray;
                padding-bottom: 8px;
            }

            &-value {
                font-size: 32px;
                font-weight: 600;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        &__saves {
            display: flex;
            flex-wrap: wrap;
            border: 1px solid $gray;
            border-radius: 4px;

            &__title {
                width: 100%;
                border-bottom: 1px solid $gray;
                padding: 8px 8px;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                margin: 0;
            }

            &__item {
                border-bottom: 1px solid $gray;
                padding: 8px 8px;
                display: inline-flex;
                width: 50%;
                justify-content: flex-start;
                align-items: center;
                margin: 0;
                font-size: 14px;

                &:nth-child(odd) {
                    border-left: 1px solid $gray;
                }

                &:last-child, &:nth-last-child(2) {
                    margin-bottom: 0;
                    border-bottom: 0;
                }

                &-value {
                    margin-left: auto;
                    font-weight: 600;
                }
            }
        }
    }
